<template>
  <el-footer>Footer</el-footer>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  computed: {
    onRoutes () {
      return this.$route.path.replace('/', '')
    }
  }
}
</script>

<style scoped>
.foot {
  background-color: red;
}
</style>



