<template>
  <div>
    <el-container>
      <el-header>
        <v-header></v-header>
      </el-header>
      <el-main>
        <el-row :gutter="24">
          <el-col :span="16" :offset="2">
            <v-content></v-content>
          </el-col>
          <el-col :span="4">
            <v-right></v-right>
          </el-col>
        </el-row>
      </el-main>
      <el-footer>
        <v-foot></v-foot>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import vHeader from './Header.vue'
import vRight from './Right.vue'
import vContent from './Content.vue'
import vFoot from './Foot.vue'
export default {
  data () {
    return {
      currentDate: '2019-05-01'
    }
  },
  components: {
    vHeader, vRight, vContent, vFoot
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-header, .el-footer {
  padding-left: 200px;
  background-color: #545c64;
  color: #333;
  text-align: center;
  line-height: 60px;
}
.el-main {
  background-color: #E9EEF3;
  color: #333;
}
</style>



